<template>
	<div class="set_nickname">
		<van-cell-group>
			<van-field
			v-model="age"
			label="年龄"
			:error="!!$vuelidation.error('age')" />
		</van-cell-group>

		<div class="bottom_btn">
			<van-button size="large" type="danger" @click="save">保存</van-button>
		</div>
	</div>
</template>


<script>
	import { USER_PROFILE } from '@/api/user';
	import valid from '@/api/user';

	export default {
		data(){
			return {
				age: ""
			}
		},

		vuelidation: {
			data: {
				age: {
					required: {
						msg(){
							return "请填写年龄"
						}
					}
				}
			}
		},

		created(){
			this.getAge();
		},

		methods: {
      getAge(){
				this.age = localStorage.getItem('age') || '';
			},
			save(){
				if(this.mixValid()){
					this.$reqPut(USER_PROFILE, {age: this.age}).then(res => {
						localStorage.setItem('age', res.data.data.age);
						 return this.$dialog.alert({message: '保存成功'})
					}).then(() => {
						this.$router.go(-1);
					})
				}
			},
		}
	}
</script>


<style scoped>
	.bottom_btn{
		padding: 30px 15px 0 15px;
	}
</style>



